.tra_wrap{
    height: 100%;
    .param_area{
        width: 100%;
        border-radius: 10px;
        padding: 10px;
        border:1px solid #bbb;
        &:focus{
            outline: none;
        }
    }
    .btn_line{
        padding: 10px 0;
    }
    .result_box{
        height: 200px;
        word-break:break-all;
        overflow-y: auto;
        border:1px solid #bbb;
        background: #fff;
        border-radius: 10px;
        padding: 10px;
        box-sizing: border-box;
    }
}